body {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  line-height: 1.5;
  background-color: #243A74;
  color: #fff;
}

p {
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 180rem;
  margin-left: auto;
  margin-right: auto;
}

h1, h2, h3 {
  margin: 0 0 6rem 0;
  font-family: "Prompt", sans-serif;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 3.8rem;
}

h3 {
  font-size: 2.8rem;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-padding-top: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
  aspect-ratio: 16/9;
  object-fit: cover;
}

input, button, textarea, select {
  font: inherit;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.header {
  background: rgb(126, 26, 124);
  background: linear-gradient(124deg, rgb(126, 26, 124) 0%, rgb(36, 58, 116) 100%);
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 3em;
  color: #fff;
}
.header .header__brand {
  width: auto;
  display: flex;
  align-items: center;
}
.header .header__brand .brand__logo {
  width: 5rem;
  height: 5rem;
}
.header .header__brand .brand__title {
  margin: 0 1rem;
  font-size: 3rem;
}
.header .nav.header__nav {
  display: flex;
  gap: 4rem;
}
.header .nav.header__nav .nav__link {
  color: #fff;
}

.hero {
  position: relative;
  max-height: 85rem;
  min-height: 50rem;
  overflow: hidden;
}
.hero .overlay {
  position: absolute;
  background: rgb(64, 54, 88);
  background: linear-gradient(45deg, rgba(64, 54, 88, 0.7976365546) 0%, rgba(36, 58, 116, 0.8993172269) 100%);
  height: 100%;
  width: 100%;
}
.hero .overlay .hero__content {
  z-index: 9;
  margin-top: 5%;
}
.hero .overlay .hero__content .title {
  color: rgba(255, 255, 255, 0.55);
  font-size: 6rem;
  text-align: center;
  line-height: 0.9;
}
.hero .overlay .hero__content .title span {
  font-size: 18rem;
}
.hero .overlay .hero__content .super-text {
  text-align: center;
  font-size: 5rem;
  font-weight: 700;
  color: #fff;
}
.hero video {
  width: 100%;
  display: block;
}

.hero__info {
  display: flex;
  justify-content: space-between;
}
.hero__info .info__item {
  max-width: 600px;
  height: 300px;
  text-align: center;
  text-transform: uppercase;
  font-family: "Prompt", sans-serif;
}
.hero__info .info__item .hero__text {
  font-size: 3rem;
  color: #fff;
  font-weight: 500;
}
.hero__info .info__item .hero__text span {
  font-weight: 300;
}
.hero__info .info__item .hero__text .darked {
  color: #5EDABE;
  font-size: 5rem;
}
.hero__info .info__item .hero__text--purple {
  color: #5EDABE;
}

.about .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 8rem 2rem;
}

.content__wrapper {
  padding: 5rem 2.8rem;
  flex: 0 0 35%;
  background-color: #121d3a;
  border-radius: 1rem;
}

.image__wrapper picture img {
  border-radius: 1rem;
}
.image__wrapper .image__caption {
  font-size: 1rem;
}

.line-up {
  margin: 7rem auto;
}
.line-up h2 {
  text-align: center;
}

.line-up__container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.line-up__container button {
  margin-left: 1rem;
  margin-right: 1rem;
  border: none;
  background-color: rgba(255, 255, 255, 0.45);
  border-radius: 50%;
  padding: 0.75rem;
  height: 2em;
  width: 2em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.line-up__container button i {
  color: #243A74;
}
.line-up__container button:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.65);
}

.carousel {
  height: 600px;
  width: 500px;
  min-width: 350px;
  overflow: hidden;
}

.image-container {
  display: flex;
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
}
.image-container picture {
  display: block;
  width: 100%;
  height: 600px;
}
.image-container picture img {
  border-radius: 1rem;
}